import React from "react";
import ReactDOM from "react-dom";
import { HashRouter } from "react-router-dom";
import { createStore, applyMiddleware, compose, combineReducers } from "redux";
import { Provider } from "react-redux";
import thunk from "redux-thunk";

import "./index.css";
import "./assets/icons/css/weather-icons.css";
import "./assets/icons/css/weather-icons-wind.css";
import App from "./App";
import geolocationReducer from "./store/reducers/geolocationReducer";
import generalReducer from "./store/reducers/generalReducer";
import searchReducer from "./store/reducers/searchReducer";
import currentWeatherReducer from "./store/reducers/currentWeatherReducer";

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const rootReducer = combineReducers({
  geolocationReducer,
  generalReducer,
  searchReducer,
  currentWeatherReducer,
});

const store = createStore(
  rootReducer,
  composeEnhancers(applyMiddleware(thunk))
);

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <HashRouter basename="/">
        <App />
      </HashRouter>
    </Provider>
  </React.StrictMode>,
  document.getElementById("root")
);
